/* CSS file for the navigation bar */
.navbar {
    height: 100%;
    width: 100px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #FBFBFD;
    overflow-x: hidden;
    padding-top: 20px;
}

/* Remove the underline for all Links */
.no-underline{
    text-decoration: none;
}

/* Text for selected page */
.selectedText {
    color: red;
}

/* Text for unselected page */
.unselectedText {
    color: black;
}

/* Text for selected page */
.selectedIcon{
    fill: red;
    stroke: red;;
}

/* Text for unselected page */
.unselectedIcon {
    color: black;
}